import React,{useEffect, useState, useRef} from "react";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Breadcrumb from "../Breadcrumb";
import { Container, Row, Col } from "react-bootstrap";
import placeholderImg from "../../../assets/images/image-placeholder.png";
import { apiGet } from "../../../utilities/axiosInstance";
import API_ENDPOINTS from "../../../utilities/apiEndPoints";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useAuth } from "../../../context/AuthContext";
import formatDate from '../../../Components/FormatDate'
import AutocompleteAddress from '../../../Components/AutocompleteAddress';
import axios from "axios";
import toDDMMYYYY from '../../../Components/NewFormatDate'


export default function ChooseLocation() {
    const navigate = useNavigate();
    const [loading, setLoading] = useState(true);
    const [isProcessing, setIsProcessing] = useState(false);
const {learnerBookData, setLearnerBookData, addressSuburb, setAddressSuburb, bookWithNewTrainerID, setBookNow, token} = useAuth();
const [trainerAvail, setTrainerAvail]= useState([])
const [lessnSummary, setLessnSummary]= useState(learnerBookData.lessonBookingData) //this is for when come to same page it was blank array initially, so add the context data
//console.log(learnerBookData)

useEffect(()=>{
},[learnerBookData])


useEffect(()=>{
 // console.log(learnerBookData?.bookWithTrainerID)
    const fetchTrainerAvailabilty = async ()=>{
        try{
          const trainerAvailResponse= await apiGet(`${API_ENDPOINTS.TRAINER_AVAILABILTY}/${bookWithNewTrainerID || learnerBookData?.bookWithTrainerID}`);
          if(trainerAvailResponse.success){
             setTrainerAvail(trainerAvailResponse?.data?.processedAvailabilities)
          }
        }
        catch(error){
         // console.log('Error fetching in instructor available', error)
        }
    }


    fetchTrainerAvailabilty();
    
  },[learnerBookData?.bookWithTrainerID, bookWithNewTrainerID])


  const [lessnDuration, setLessnDuraion]=useState(1);
  const [selectedDate, setSelectedDate]=useState();
  const [selectedTime, setSelectedTime]= useState();
 
  const totalBookHourPriceRef = useRef(0);
  useEffect(() => {
    totalBookHourPriceRef.current = learnerBookData.totalBookHourPrice;
}, [learnerBookData.totalBookHourPrice]);

const learnerBookNow= async(newLessnSummary)=>{
   // e.preventDefault();
  // console.log(learnerBookData.totalBookHourPrice)
    setIsProcessing(true);
    // Prepare the payload for the API call
    const payload = {
      // amount: Number(+learnerBookData.totalBookHourPrice + +learnerBookData.totalTestPackagePrice),
      // amount : learnerBookData.perHourPriceList.filter((item, i)=> (
      //   item.hours === lessnSummary?.reduce((total, entry)=>total + (entry.lesson_hours || 0), 0) ))[0]?.price,
      amount:Number(totalBookHourPriceRef.current),
      address: learnerBookData.preferred_pickup_address,
      instructor_id: Number(bookWithNewTrainerID || learnerBookData.bookWithTrainerID),
      optional_msg_to_your_instructor: learnerBookData.optional_note_instructor,
      test_package_price: 0 /* Number(learnerBookData.totalTestPackagePrice)*/,
      credit_hours_price: Number(0.00),
      suburb:learnerBookData.suburb,
      state_slug:learnerBookData.state_slug,
      postcode:Number(learnerBookData.postcode),
      state_name:learnerBookData.state_name,
      lesson_details: newLessnSummary || learnerBookData.lessonBookingData
    };
  
    try {
      setIsProcessing(true);
      // Make the API call
      const response = await axios.post(
        `${process.env.REACT_APP_API_BASE_URL || "https://ready-set-driving-school.pwd.net.au"}${API_ENDPOINTS.LEARNER_BOOK_NOW}`,
        payload,
        { headers: { "Content-Type": "application/json", "Authorization": `Bearer ${token}`}, }
      );
  
     // console.log("API Response:", response.data);
      if (!response.data.success) {
        toast.error(response.data.message || "Request failed.");
        setIsProcessing(false);
        return;
      }
else{
  const bookNowResponse = response.data.data;
// const clientSecret = response.data.data.client_key;
setBookNow(bookNowResponse);
navigate("/learner/book-lesson")
     // console.log(bookNowResponse, clientSecret)
     }       
      // Additional logic here if required (e.g., handling Stripe payment)
    } catch (error) {
                    //console.error("API call error:", error);
                    // Handle Axios errors
                    if (error.response) {
                                      toast.error(
                                      error.response.data?.data.error ||
                                      error.response.statusText ||
                                      "An error occurred with the request."
                                      );
                    } else if (error.request) {
                                      toast.error("No response received from the server. Please try again.");
                    } else {
                                      toast.error("An unexpected error occurred. Please try again.");
                    }
    } finally {
      setIsProcessing(false);
    }
  };

 
  const changeLessnDuration=(e)=>{
   setLessnDuraion(Number(e.target.value));
  // console.log(e.target.value)
  }
  const changeAvailDate=(e)=>{
   setSelectedDate(e.target.value)
   //console.log(e.target.value)
  }
  const changeAvailTime=(e)=>{
  // console.log(e.target.value)
  setSelectedTime(e.target.value)
  }  

  let addFormFields = () => {
    //console.log(lessnSummary)
    const isDuplicate = lessnSummary.some(
      (item) =>
        item.lesson_hours === lessnDuration &&
        item.lesson_date === selectedDate &&
        item.lesson_time === selectedTime
    );
   //navigate("/learner/book-lesson");
  
    if (!isDuplicate) {
      setLessnSummary([...lessnSummary, { lesson_hours: lessnDuration, lesson_date: selectedDate, lesson_time: selectedTime }]);
      let newLessnSummary = [...lessnSummary, { lesson_hours: lessnDuration, lesson_date: selectedDate, lesson_time: selectedTime }];
      //{learnerBookData?.lessonBookingData?.reduce((total, entry)=>total + (entry.objDuration || 0), 0)}
    //  setLearnerBookData({...learnerBookData, lessonBookingData:[...lessnSummary, { objDuration: lessnDuration, objDate: selectedDate, objTime: selectedTime }]})
      setTimeout(()=>{
       // navigate("/learner/book-lesson");
       learnerBookNow(newLessnSummary)
      },200)
     //navigate("/learner/book-lesson");
     // console.log(lessnSummary)
    } else {
        setTimeout(()=>{
           // navigate("/learner/book-lesson");
           let newLessnSummary = [...lessnSummary, { lesson_hours: lessnDuration, lesson_date: selectedDate, lesson_time: selectedTime }];
           learnerBookNow(newLessnSummary)
          },200)
    }
  };

  const clearLessonsList=()=>{//this function is for clear all the stored lesson on cancel click event in ui
   // setLessnSummary()
   setLearnerBookData(prevState => {
    return {
      ...prevState,
      lessonBookingData: [],
      totalBookHours:0,
      totalBookHourPrice:0,
      
  };
   })
  }

  useEffect(() => { //this is for the update the map function whenever lessnSummary has new changes
    // const updatedLessonBookingData = lessnSummary.map(lesson => ({
    //   lesson_hours: lesson.lesson_hours,
    //   lesson_date: lesson.lesson_date,
    //   lesson_time: lesson.lesson_time,
    // }));
    // setLearnerBookData({...learnerBookData, 
    //     lessonBookingData:updatedLessonBookingData, 
    //     totalBookHours:lessnSummary?.reduce((total, entry)=>total + (entry.lesson_hours || 0), 0),
    //     totalBookHourPrice:learnerBookData.perHourPriceList.filter((item, i)=> (
    //         item.hours === lessnSummary?.reduce((total, entry)=>total + (entry.lesson_hours || 0), 0) ))[0]?.price,
    //     }
       
    // )


    setLearnerBookData(prevState => {
      const totalBookHours = lessnSummary?.reduce((total, entry) => total + (entry.lesson_hours || 0), 0);
      const totalBookHourPrice = prevState.perHourPriceList.find(
          item => item.hours === totalBookHours
      )?.price || 0;
      //console.log(totalBookHourPrice)
      return {
          ...prevState,
          lessonBookingData: lessnSummary?.map(lesson => ({
              lesson_hours: lesson.lesson_hours,
              lesson_date: lesson.lesson_date,
              lesson_time: lesson.lesson_time,
          })),
          totalBookHours,
          totalBookHourPrice,
          
      };
  });


    
  }, [lessnSummary])


    useEffect(() => {
        const fetchData = async () => {
            try {
              const [ lessonHoursRes  ] = await Promise.all([ apiGet(API_ENDPOINTS.PRICES) ]);
              if (lessonHoursRes.success && lessonHoursRes.data?.hours_list) {
                setLearnerBookData({...learnerBookData, perHourPriceList:lessonHoursRes.data.hours_list})
              } else {
               // console.error("Unexpected lesson hours response:", lessonHoursRes);
              }
              
            } catch (error) {
             // console.error(error); // Log error for debugging
            } finally {
              // dispatch(finishLoading());
            }
          };

      fetchData();
    }, []);



  return (
    <div>
      <div className="mainContent learnerDashboard">
        <Breadcrumb />
        <div className="mainContent__assets">
          <Container>
            <Row>
              <Col lg={12}>
                <div className="learnerDashboard__wrp">
                  <div className="secIntro">
                    <div className="secIntro__left">
                      <h2>Choose Location</h2>
                    </div>
                  </div>
                </div>
                <div className="learnerDashboard__wrp">
                  <div className="learnerDashboard__left">
                    <div className="bookNow__drivingLession">
                        <form>
                            <div className="bookNow__drivingLessons driving-test-package">
                                <div className="bookNow__drivingLessonsHead d-flex align-items-center">
                                    <h3>Driving Lesson 1</h3>
                                </div>
                                <div className="bookNow__drivingLessonsInner bg-white d-flex flex-column gap-20">
                                    <div className="bookNow__lessonDuration w-100">
                                        <label htmlFor="Lesson duration">Lesson duration</label>
                                            <div className="d-flex gap-30">
                                            <div className="d-flex gap-10 align-items-center">
                                            <label><input type="radio" name="lessonDuration" value={1} className={lessnDuration === 1 ? 'active':''} checked={lessnDuration === 1}  required onChange={changeLessnDuration}/><span>1 hour</span></label>
                                            </div>
                                            <div className="d-flex gap-10 align-items-center">
                                            <label><input type="radio" name="lessonDuration" value={2} className={lessnDuration === 2 ? 'active':''} checked={lessnDuration === 2}  required onChange={changeLessnDuration}/><span>2 hours</span></label>
                                            </div>
                                            </div>
                                    </div>
                                    <div className="bookNow__times d-flex gap-20 w-100">
                                        <div className="d-flex flex-column col-12 col-sm-6 col-md-6 gap-10">
                                            <label htmlFor="Available Dates">Available Dates</label>
                                            <select name="availableDates" id="" required onChange={changeAvailDate}>
                                            <option value="" disabled selected>Select Date</option>
                                            {trainerAvail?.filter((item1) => item1.lesson_hours === Number(lessnDuration)).length > 0 ?   
                                            trainerAvail?.filter((item1) => item1.lesson_hours === Number(lessnDuration))
                                            .reduce((unique, item) => {
                                            // Check if we already have this date in our unique array
                                            const exists = unique.find(u => u.availability_date === item.availability_date);
                                            if (!exists) {
                                            unique.push(item);
                                            }
                                            return unique;
                                            }, [])
                                            .map((item, i) => (
                                            <option value={item.availability_date} key={i}>
                                            {item.formatted_date}
                                            </option>
                                            ))
                                            : <><option>No Date available</option></>
                                            }
                                            </select>
                                        </div>
                                        <div className="d-flex flex-column col-12 col-sm-6 col-md-6 gap-10">
                                            <label htmlFor="Available Times">Available Time</label>
                                            { learnerBookData?.bookWithTrainerID !== null ? 
                                            <select name="availableTimes" id="" required onChange={changeAvailTime}>
                                            <option value="" disabled selected>Select Time</option>
                                            {trainerAvail?.filter((item1) => (item1.lesson_hours === Number(lessnDuration) && item1.availability_date === selectedDate)).map((item, i)=>(
                                            <option value={item.availability_start_time} key={i}>{item.availability_start_time} - {item.availability_end_time}</option>
                                            ))}

                                            </select> :  

                                            <select name="availableSlots" id="" required onChange={changeAvailTime}>
                                            <option value="" disabled selected>Select Time</option>
                                            {selectedDate ? 
                                            trainerAvail
                                            ?.filter((item1) => (
                                            item1.lesson_hours === Number(lessnDuration) && 
                                            item1.availability_date === selectedDate
                                            ))
                                            .map((item) => 
                                            item.slots?.map((slot, index) => (
                                            <option value={slot.availability_start_time} key={index}>
                                            {slot.availability_start_time} - {slot.availability_end_time}
                                            </option>
                                            ))
                                            )

                                            :""}
                                            </select>
                                            }
                                        </div>
                                    </div>

                                    <div className="bookNow__times d-flex gap-20 w-100"><label >Location for Lesson Pickup</label></div>
                                    <div className="bookNow__times d-flex gap-20 w-100">
                                    <div className="d-flex flex-column col-12 col-sm-12 col-md-12 gap-10 RegistrationForm__field">
                                      <AutocompleteAddress setAddressSuburb={setAddressSuburb} 
                                     // billingAddrs={addressSuburb.length > 0  ? `${addressSuburb[0]?.long_name} ${addressSuburb[1]?.long_name} ${addressSuburb[3]?.long_name} ` : ""} //pickupaddress is for initial address value
                                     billingAddrs={addressSuburb.length > 0  ? `${addressSuburb[0]?.long_name} ${addressSuburb[1]?.long_name} ${addressSuburb[2]?.short_name} ${addressSuburb[addressSuburb.length - 3]?.long_name} ${addressSuburb[addressSuburb.length - 2]?.long_name} ${addressSuburb[addressSuburb.length - 1]?.long_name}`: ""}
                                     //billingAddrs is when click showing the full address in input
                                     />
                                    </div>
                                    </div>
                                    <div className="bookNow__times d-flex gap-20 w-100">
                                    <div className="d-flex flex-column col-12 col-sm-6 col-md-6 gap-10 RegistrationForm__field">
                                      <input type="text" value={addressSuburb[1]?.long_name}/>
                                    </div>
                                    <div className="d-flex flex-column col-12 col-sm-6 col-md-6 gap-10 RegistrationForm__field">
                                      <input type="text" value={addressSuburb[3]?.long_name}/>
                                    </div>
                                    </div>
                                    <div className="d-flex flex-column col-12 col-sm-12 col-md-12 gap-10 RegistrationForm__field">
                                    <label htmlFor="Available Dates">Optional message to your Instructor</label>
                                      <textarea value={learnerBookData.optional_note_instructor} 
                                      onChange={(e) => setLearnerBookData({
                                        ...learnerBookData, 
                                        optional_note_instructor: e.target.value
                                      })}
                                      >

                                      </textarea>
                                    </div>
                                    

                                    <div className="bookNow__buttons d-flex justify-content-between">
                                        <button type="button" className="btn btn-blue-outline" onClick={() => {navigate(-1);clearLessonsList()}}>Cancel</button>
                                        <button type="button" onClick={()=>{ addFormFields()}} className="btn btn-blue"
                                        abc={lessnDuration === 'undefined' || selectedDate === 'undefined' || selectedTime === 'undefined' ?'abc':'xyz'}
                                        disabled={!lessnDuration || !selectedDate || !selectedTime || !learnerBookData.preferred_pickup_address }
                                        >{isProcessing ? 'Validating...': 'Continue > '}</button>
                                    </div>
                                </div>
                            </div>
                        </form>
                </div>
                  </div>
                  <div className="learnerDashboard__right">
                    <div className="bookNow__drivingLessons">
                      <div className="bookNow__drivingLessonsHead text-black bg-light-grey">
                        <h3>Your chosen Instructor</h3>
                      </div>
                      <div className="learnerDashboard__mid">
                        <div className="learnerDashboard__profile">
                          <img src={learnerBookData?.bookTrainerInfo?.profile_details?.profile_picture  || placeholderImg} alt="" />
                        </div>
                        <div className="learnerDashboard__instructorInfo">
                          <h4>{learnerBookData?.bookTrainerInfo?.first_name} {learnerBookData?.bookTrainerInfo?.last_name}</h4>
                          <div className="learnerDashboard__profileCarInfo">
                            <img src={learnerBookData?.bookTrainerInfo?.instructor_vehicle?.vehicle_image  || placeholderImg} alt="" />
                            <div className="learnerDashboard__carInfo">
                              <p>{learnerBookData?.bookTrainerInfo?.instructor_vehicle?.make} &nbsp;
                              {learnerBookData?.bookTrainerInfo?.instructor_vehicle?.vehicle_model} &nbsp;
                              {learnerBookData?.bookTrainerInfo?.instructor_vehicle?.vehicle_year} &nbsp;
                              ({learnerBookData?.bookTrainerInfo?.transmission_name} )

                              </p>
                              <span className="pdpWrapper__profileCarInfo__ncaprating">
                                <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg">
                                  <path d="M5 8.3747L7.42493 9.90932C7.86901 10.1906 8.41243 9.77481 8.29556 9.24901L7.65281 6.36318L9.79727 4.41892C10.1888 4.0643 9.9784 3.39176 9.4642 3.34896L6.64194 3.09829L5.53757 0.371428C5.33891 -0.123809 4.66109 -0.123809 4.46243 0.371428L3.35806 3.09217L0.535797 3.34285C0.021595 3.38565 -0.18876 4.05819 0.202734 4.4128L2.34719 6.35707L1.70444 9.24289C1.58757 9.7687 2.13099 10.1845 2.57507 9.90321L5 8.3747Z"
                                    fill="#FFD53E"></path>
                                </svg>
                                &nbsp;{learnerBookData?.bookTrainerInfo?.instructor_vehicle?.ancap_rating}-star ANCAP rating
                              </span>
                              <span className="pdpWrapper__profileCarInfo__carno">
                                {learnerBookData?.bookTrainerInfo?.instructor_vehicle?.vehicle_no}
                              </span>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="bookNow__drivingLessons">
                      <div className="bookNow__drivingLessonsHead text-center text-black bg-light-grey">
                        <h4>Your Current Bookings</h4>
                      </div>
                      <div className="bookNow__subTotal bg-white">
                        {learnerBookData.lessonBookingData.length > 0 ? 
                        <>
                       {/* {learnerBookData?.lessonBookingData?.map((item, i)=>( */}
                            <div className="learnerDashboard__bottom">
                                <div className="learnerDashboard__subTotalrow d-flex justify-content-between align-items-center">
                                <strong>
                                {learnerBookData?.lessonBookingData?.length} x Driving Lesson{learnerBookData?.lessonBookingData?.length >1?'s':''} (
                                    {/* {learnerBookData?.lessonBookingData?.reduce((total, entry)=>total + (entry.objDuration || 0), 0)} */}
                                    {learnerBookData?.totalBookHours}
                                     Hour{learnerBookData?.totalBookHours>1?'s':''})
                                
                                <span> 
                                   {toDDMMYYYY(learnerBookData?.lessonBookingData[0]?.lesson_date)}&nbsp;
                                   {learnerBookData?.lessonBookingData[0]?.lesson_time}</span>
                                </strong>
                                <p> ${learnerBookData?.totalBookHourPrice}</p>
                               
                                </div>
                            </div>
                        {/* ))} */}
                        

                        <div className="font-secondary bookNow__subTotalrow d-flex justify-content-between align-items-center border-dark">
                          <strong>Subtotal</strong>
                          <strong>${learnerBookData?.totalBookHourPrice}</strong>
                        </div>
                        <div className="font-secondary border-y-0 learnerDashboard__bottom bg-light-grey border-grey padding-sm">
                          <div className="learnerDashboard__subTotalrow d-flex justify-content-between align-items-center">
                            <span className="p-0">Credit Applied</span>
                            <p>-$0.00</p>
                          </div>
                        </div>
                        <div className="font-secondary bookNow__subTotalrow d-flex justify-content-between align-items-center">
                          <h4>Total Amount</h4>
                          <h4>${learnerBookData?.totalBookHourPrice}</h4>
                        </div>
                        </>
                        : <><div className="learnerDashboard__bottom"><h4>No lesson confirmed</h4></div></>
                        }

                      </div>
                    </div>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
      <ToastContainer />
    </div>
  );
}
